<template>
    <section class="page-setting">
        <span class="iconfont iconclose ic-close" @click="onClose"></span>

        <h2>默认设置</h2>
        <div class="form-item" v-for="item in list" :key="item.key">
            <span>{{ item.label }}</span>
            <v-input v-model="item.value" />
        </div>
        <div class="btn-container">
            <el-button type="primary" @click="onSave">保存</el-button>
        </div>
    </section>
</template>

<script>
import { mapState, mapActions } from 'vuex'
export default {
    name: 'Setting',
    data() {
        return {
            list: [
                // {
                //     key: 'shape',
                //     label: '形状',
                //     value: ''
                // },
                {
                    key: 'density',
                    label: '密度',
                    value: ''
                },
                {
                    key: 'cost',
                    label: '成本单价',
                    value: '',
                },
                {
                    key: 'price',
                    label: '单价',
                    value: '',
                },
            ]
        }
    },
    computed: {
        ...mapState('quote', ['initData'])
    },
    watch: {
        initData: {
            immediate: true,
            deep: true,
            handler(data) {
                if(data) {
                    this.list.some((item, index) => {
                        if(data[item.key]) {
                            this.$set(this.list[index], 'value', data[item.key])
                        }
                    })
                }
            }
        }
    },
    methods: {
        ...mapActions('quote', ['setInitData']),
        onClose() {
            this.$emit('close')
        },
        onSave() {
            const result = this.list.reduce((result, item) => {
                if(item.value) {
                    result[item.key] = item.value
                }
                return result
            }, {})
            this.setInitData(result)
            this.$emit('close')
            this.$message({
                message: '居然让你修改成功了',
                type: 'success'
            })
        }
    }
}
</script>

<style lang="scss" scoped>
.page-setting {
    position: relative;
}
.ic-close {
    position: absolute;
    top: 0px;
    right: 0;
    font-size: 16px;
    color: gray;
}
h2 {
    margin-bottom: 20px;
}
.form-item {
    padding-bottom: 10px;
    span:first-child {
      display: block;
      padding-bottom: 5px;
      font-weight: 500;
    }
  }
  .btn-container {
      margin-top: 20px;
      text-align: right;
  }
</style>